body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

header {
	background: #f2c3b0;
	width: 100%;
    padding: 0;
    overflow-x: hidden;
}

header div {
	color: #333;
	margin-left: 20px;
	margin-right: 20px;
}

#top-nav{
	-ms-flex-pack: center;
	    justify-content: center;
}

.filter-form {
	width: 100%; 
    display: -ms-flexbox; 
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-align: start;
        align-items: flex-start;
    margin-left: 20px;
    margin-bottom: 20px;
}

.flex-container {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: center;
	    align-items: center;
}

#filter-container {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-line-pack:start;
	    align-content:flex-start;
	margin-top:5px;
	margin-bottom:15px;
}

.headerOptSpan {
	display: -ms-flexbox;
	display: flex;
	margin:0;
}

.headerOptTxt {
	margin-right: 3px;
	margin-left: 3px;
}

.card {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: column wrap;
	    flex-flow: column wrap;
	border: 1px #333 solid;
	width: 180px;
	margin: 10px;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: start;
	    justify-content: flex-start;
	background-color: white;
	padding: 5px;
	max-height: 220px;

}

.card:hover {
	-webkit-box-shadow: 10px 10px 5px #888888;
	        box-shadow: 10px 10px 5px #888888;
}

h2, h3 {
	margin: 0;
}

.card-bottom {
	width: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: space-evenly;
	    justify-content: space-evenly;
}

.card img {
	width: 160px;
	height: 160px;
	margin: 3px;
}

#search-span {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	margin-left: 20px;
}

#sort-by {
	display: -ms-flexbox;
	display: flex;
    -ms-flex-align: end;
        align-items: flex-end;
    padding: 0;
    margin: 0;
}

#sort-by p {
	margin-right: 15px;
}

#sortBar {
	grid-column-start: 2;
	grid-column-end: 3;
	grid-row-start: 1;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: center;
	    align-items: center;
	padding: 10px;
	margin:10px;
	margin-bottom: 0;
	padding-bottom: 0;
	margin-right: 100px;
}

#filterBar {
	grid-column-start: 1;
	grid-row-start: 2;
	grid-row-end: 3;
	background-color: white;
	border-right: 1px gray solid;
}

h2{
	font-size: 1.1em;
}

h3 {
	font-size: 0.9em;
}

#filter-list {
	background-color: white;
	padding: 10px;
	margin:0;
	display: -ms-flexbox;
	display: flex;
	grid-column-start: 2;
	grid-row-start: 2;
	-ms-flex-flow: row wrap;
	    flex-flow: row wrap;
}

#mainDisplay {
	display: grid;
	grid-template-rows: 25% 75%;
	grid-template-columns: 250px 1fr;
}

#submit-search {
	background: #0D59C0;
	background-size: 70%;
	border-radius: 3px;
	width: 25px;
	height: 25px;
	padding: 4px;
	margin-left: 5px;
	border: none;
	background-repeat:no-repeat;
	background-position: center center;
}

#header-options {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: row wrap;
	    flex-flow: row wrap;
	-ms-flex-align: center;
	    align-items: center;
	width: 350px;
}

#search-bar {
	border-radius: 3px;
}


